	module.exports = {"data":{"markdownRemark":{"html":"<h2>Intent</h2>\n<p>This is the first optional step. Frontend isn't for everyone and developers can get away without it for the most part if the REALLY want to.\nThat being said, this step is still recommended. </p>\n<p>It is a fairly free step with most of the onus on the mentee to take this as far as they want. From creating a simple form to a full web application.</p>\n<p>Your job as a mentor is to give them guidance on the tools and frameworks for them to use. Try and pick something that is established but not too old, as of 2018 Angular 3? and React are the hot topics. </p>\n<p>Something that a lot of developers struggle with is the actual design of the UI. An iterative approach is recommended here and the style will develop over time. If the mentee is very keen on this aspect however, encourage them to sketch out low fidelity designs before attempting to code.</p>\n<h2>Problem</h2>\n<p>Create a rich frontend that will communicate with the REST api you created in the previous step.</p>\n<ul>\n<li>You can choose any frontend framework you want. (Angular or React is recommended, 2018)</li>\n<li>If it’s a javascript framework, ensure it has an appropriate build and package system(s) (Npm/Yarn/Gulp/Webpack) </li>\n</ul>\n<p>You can take this step as far as you want.</p>\n<h3>Tips:</h3>\n<ul>\n<li>This is more about functionality than form. Focus more on the interactions and less on the style of the frontend.  </li>\n<li>Don’t worry about authentication but you can add this as an extension if you want.  </li>\n<li>Start with a tutorial in your given framework. Pluralsight is a good resource for this.</li>\n</ul>\n<p><a href=\"../shopping3\">&#x3C; Prev</a> | <a href=\"../shopping4\">Next ></a></p>","frontmatter":{"path":"/shopping3_5","title":"Shopping Cart 3.5: REST + JS"}}},"pathContext":{}}

